import {isMobile} from 'react-device-detect';
import SongTitle from "components/spore/SongTitle.js";
import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import StemPlayer from "components/spore/StemPlayer.js";
import './demo.scss';

const Demo = ({name}) => { 
    let className = "spore showing-spore-player demo";
    if (name.includes("doodles")) {
        className += " doodles";
    }
    if (name.includes("ragnarok")) {
        className += " ragnarok";
    }
    let artist = "Keyon Christ";
    let song = "Twisting Faith";

    if (name === "doodles1") {
        artist = "Doodles";
        song = "Spaceships";
    }

    if (name === "jeremih") {
        artist = "Jeremih";
        song = "Don\'t Tell 'Em";
    }
    return (
        <div className={className}>
          {name === "jeremih" && 
          <video
            playsInline
            className="rain-video"
            autoPlay
            loop
            src={isMobile ? "/mobile-rain.mp4" : "/desktop-rain.mp4"}/>}
          <div className="caurosel player-chosen">
            <div className="spore-player-option chosen">
              <SongTitle artist={artist} song={song}/>
            </div>
          </div>
          <StemPlayer tokenId={name}/>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Demo);
